@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700&display=swap");
* {
    margin: 0;
    padding: 0;
    font-family: "Open Sans", sans-serif;
}

body{
    box-sizing: border-box;
    display: flex;
    padding: 0 10px;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #5E63BA;
}

.main{
    display: flex;
    width: 850px;
    gap: 70px;
    padding: 60px 40px;
    background: #fff;
    border-radius: 10px;
    align-items: flex-end;
    justify-content: space-between;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
}

.hange-img{
    width: 270px;
}

.hange-title{
    font-size: 1.45rem;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
    text-transform: uppercase;
}

.endGame-shadow{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    z-index: 999s;
    transition: opacity 0.4s ease;
}

.endGame-continer{
    z-index: 9999;
    position: absolute;
    padding: 30px;
    width: 100%;
    max-width: 360px;
    border-radius: 10px;
    background: #fff;
    text-align: center;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
}

.endGame-img{
    width: 130px;
    margin-bottom: 20px;
}

.endGame-title{
    font-size: 1.53rem;
}

.endGame-subtitle{
    font-size: 1.15rem;
    margin: 15px 0 30px;
    font-weight: 500;
}

.endGame-word{
    color: #5E63BA;
    font-weight: 600;
}

.endGame-btn{
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 4px;
    text-transform: uppercase;
    background: #5E63BA;
    padding: 12px 23px;
}

.letters-container{
    gap: 10px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.letters{
    width: 28px;
    font-size: 2rem;
    text-align: center;
    font-weight: 600;
    margin-bottom: 40px;
    text-transform: uppercase;
    border-bottom: 3px solid #000;
}

.hint,.wrongGuesses{
    text-align: center;
    font-size: 1.1rem;
    font-weight: 500;
    margin-bottom: 15px;
}

.wrongGuesses span{
    color: #ff0000;
}

.keyboard{
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
    margin-top: 40px;
    justify-content: center;
}

.key{
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 4px;
    text-transform: uppercase;
    background: #5E63BA;
    padding: 7px;
    width: calc(100% / 9 - 5px);
}

.key:hover{
    background: #8286c9;
     
}

@media (max-width:850px) {
    .main{
        display: block;
        max-width:550px ;
    }
    .hange-img-container{
        text-align: center;
    }
}

@media (max-width:550px) {
    .main{
        max-width:300px ;
    }
}